@import url(https://fonts.googleapis.com/css2?family=Cabin&family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap);

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'montseratt', sans-serif;
}

.app{
  background-image: url(/Weather/static/media/sunny-weather.88a31020.jpg);
  background-size: cover;
  background-position: bottom;
  transition: 0.4s ease-out;
}

.app.dark-clear{
  background-image: url(/Weather/static/media/mobile-clear-night.bff65a17.jpg);
}

.app.dark-snowy{
  background-image: url(/Weather/static/media/mobile-snow-night.624b0fce.jpg);
}
.app.dark-rainy{
  background-image: url(/Weather/static/media/mobile-rain-night.653a62dd.jpg);
}
.app.dark-clouds{
  background-image: url(/Weather/static/media/mobile-clouds-night.6755fd10.jpg);
}

.app.snowy{
background-image: url(/Weather/static/media/snowy-weather.6b615c32.jpg);
}

.app.rainy{
  background-image:url(/Weather/static/media/rain-weather.f3e800a4.jpg);
}
.app.thunder-rain{
  background-image:url(/Weather/static/media/thunder-and-rain.61f8ccc4.jpg);
}
.app.thunder{
  background-image:url(/Weather/static/media/thunder-weather.fdefeb52.jpg);
}
.app.cloudy{
  background-image:url(/Weather/static/media/cloudy.aa167354.jpg);
}



.main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgb(0,0,0,0.2), rgb(0,0,0,0.75));
 padding: 25px;
}
.search-box{
  margin:0 0 75px;
  width:100%;
  
}
.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border:none;
  outline:none;
  border-color: rgba(255,255,255, 0.);
  border-radius: 22px 22px 22px 22px ;
  background-color: rgba(10,10,10,0.4);
  margin-top:-15px;
  box-shadow: 0px 5px rgba(0,0,0 0.2);
  color: #000;
  font-size: large;
  transition: 0.4s ease;
}
.search-box .search-bar:focus:hover{
  background-color: rgba(0, 0, 0, 0.9);
  color:rgba(255,255,255, 0.7);
}

.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 600;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
color: #fff;
font-size: 20px;
font-weight: 400;
font-family: 'Cabin', sans-serif;
text-align: center;
text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box{
  text-align: center;
  
}

.weather-box .temp {
  position: relative;
  display:inline-block;
  margin: 30px auto;
  background-color: rgba(0,0,0, 0.5);
  border-radius: 16px;
  padding: 15px 25px;
  color: #fff;
  font-size: 102px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color:#fff;
  font-size:48px;
  font-weight: bolder;

  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

@media only screen and (min-width: 700px ){


  .app{
    background-image: url(/Weather/static/media/desktop-sunny-day.96d8a259.jpg);
    background-size: cover;
    background-position: bottom;
    transition: 0.4s ease-out;
  }



  .app.dark-clear{
    background-image: url(/Weather/static/media/desktop-night-clear.62687e61.jpg);
  }
  
  .app.dark-snowy{
    background-image: url(/Weather/static/media/snow-desktop-night.f1141cff.jpg);
  }
  .app.dark-rainy{
    background-image: url(/Weather/static/media/desktop-rain-night.eefa9d8a.jpg);
  }
  .app.dark-clouds{
    background-image: url(/Weather/static/media/desktop-coulds-night.42f3451a.jpg);
  }
  
  .app.snowy{
  background-image: url(/Weather/static/media/desktop-snow.b0e9a969.jpg);
  }
  
  .app.rainy{
    background-image:url(/Weather/static/media/desktop-rain-day.9388a0bd.jpg);
  }
  .app.thunder-rain{
    background-image:url(/Weather/static/media/desktop-thunder-tain-day.9159612d.jpg);
  }
  .app.thunder{
    background-image:url(/Weather/static/media/desktop-thunder-tain-day.9159612d.jpg);
  }
  .app.cloudy{
    background-image:url(/Weather/static/media/desktop-clouds-day.95f5f688.jpg);
  }
  

}
